<template>
  <div class="logoin">
    <van-nav-bar
      title="登录"
      left-text="注册"
      right-text="关闭"
      :fixed="true"
      @click-left="goPage('Register')"
      @click-right="goPage('Home')"
    />
    <div class="logo">
      <img class="auto-img" src="../assets/logo.jpg" alt />
    </div>
    <van-form @submit="login">
      <van-field
        v-model="userInfo.phone"
        name="手机号"
        label="手机号"
        placeholder="11位手机号"
        autocomplete="off"
      />

      <van-field
        v-model="userInfo.password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码(6-16个字符)"
        autocomplete="off"
      />
      <div class="btn-box">
        <van-button round block type="info" native-type="submit">登录</van-button>
        <!-- <van-button round block type="info" native-type="submit">登录</van-button> -->
      </div>
      <p class="pwd">
        <span @click="goPage('Email')">找回密码</span>
      </p>
    </van-form>
  </div>
</template>

<script>
////导入表单验证模块
import { valid } from "../utils/valid";

//导入公共方法模块
import { utils } from "../utils/utils";

export default {
  name: "Login",

  created() {
    this.$store.state.vanTabbar = false;
  },

  computed: {
    userInfo() {
      return this.$store.state.loginModule.userInfo;
    }
  },

  methods: {
    //登录
    login() {
      if (valid.validForm(this.userInfo, this)) {
        //加载提示
        this.$toast.loading({
          //是否禁止背景点击
          forbidClick: true,

          //禁止自动关闭
          duration: 0,

          message: "登录中..."
        });

        this.axios({
          method: "POST",
          url: "/login",
          data: {
            appkey: this.appkey,
            phone: this.userInfo.phone,
            password: this.userInfo.password
          },
          transformRequest: utils.transformRequest
        })
          .then(result => {
            //关闭加载提示
            this.$toast.clear();

            //如果登录成功后, 将token保存在cookie中
            if (result.data.code == 200) {
              //将token分段存储
              // let tokenData = result.data.token.split(".");
              //

              //cookie过期时间
              // let expires = 30 * 24 * 60 * 60;

              // tokenData.forEach((v, i) => {
              //   //v: 一个token分段
              //   this.$cookies.set("t" + i, v, expires);
              // });
              //存储token字符串
              localStorage.setItem("_t", result.data.token);

              //跳转页面
              this.$router.push({ name: "Home" });
            } else {
              this.$toast(result.data.msg);
            }
          })
          .catch(err => {
            this.$toast.clear();
          });
      }
    },
    goPage(routerName) {
      this.$router.push({ name: routerName });
    }
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.vanTabbar = true;
  }
};
</script>

<style lang="less" scoped>
.logoin {
  background: url(../assets/login-bg.jpg) no-repeat;
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;

  overflow: hidden;
  .pwd {
    text-align: right;
    padding-right: 25px;
    color: #f2f3f5;
  }
  .title {
    text-align: center;
    font-size: 22px;
    color: #444;
  }

  .btn-box {
    margin-top: 80px;
    padding: 0 10px;
    .van-button {
      width: 200px;
      background: none;
      color: #fff;
      font-weight: 500;
      border: 1px solid #fff;
      margin: 0 auto;
    }
  }
  .logo {
    margin: 0px auto;
    padding: 80px 0 20px 0;
    width: 120px;
    height: 120px;
    img {
      height: 80%;
      border-radius: 50%;
      border: 3px solid slategrey;
    }
  }
  .van-form {
    .van-field {
      background: none;
      color: #fff;
    }
  }
}
.auto-img {
  width: 80%;
}
</style>